body {
    font-family: 'Roboto', sans-serif;
}

/* navbar style */
/* header */
.header {
    position: fixed;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 12rem;
}
.header a {
    text-decoration: none;
}
.header ul {
    list-style: none;
}
.logo {
    font-size: 2rem;
}
.menu-items {
    display: flex;
    align-items: center;
}
.menu-items li > a.nav-menu {
    padding: 0.5rem 1rem;
    border-radius: 5px;
    position: relative;
}
.menu-items li > a.nav-menu::after {
    content: '';
    position: absolute;
    top: 35px;
    width: 0%;
    height: 3px;
    background-color: #6ac9f3;
    left: 0;
    right: 0;
    margin: auto;
    transition: width 0.3s ease-in-out;
}
.menu-items li:hover a.nav-menu::after {
    content: '';
    width: 65%;
}

.menu-items li:hover > a {
    color: #6ac9f3;
}

.mega-menu-list li {
    background-color: transparent;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    transition: all 0.3s ease-in-out;
}

.mega-menu-list li:hover {
    background-color: #6ac9f3;
    color: #fff;
}

.mega-menu-list li:hover > a {
    color: #fff;
}

/* dropdown */
.dropdown {
    position: relative;
}
.dropdown-menu,
.menu-right {
    position: absolute;
    width: 100%;
    top: 50px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    background-color: #ffffff;
    left: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
}
.menu-right {
    top: 0;
    left: 110%;
}
.menu-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.dropdown:hover .dropdown-menu {
    top: 56px;
    opacity: 1;
    visibility: visible;
}
.dropdown-right:hover .menu-right {
    left: 101%;
    opacity: 1;
    visibility: visible;
}

/* mega menu  */
.mega-menu {
    position: absolute;
    left: 0;
    width: 100%;
    top: 80px;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
}
.mega-menu .content {
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
    /* box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); */
    justify-content: space-between;
}
.blog .content {
    grid-template-columns: repeat(3, 1fr);
}
.content .col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    line-height: 3rem;
}
.content .col .img-wrapper {
    display: block;
    position: relative;
    width: 100%;
    height: 20vw;
    overflow: hidden;
}
.content .col .img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.content .col img {
    width: 100%;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.content .col .img-wrapper:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.content .col h2 {
    font-size: 1.2rem;
    line-height: 3rem;
    font-weight: bold;
}
.content .col p {
    line-height: 1.2rem;
}
.content .col .mega-links {
    border-left: 1px solid #1a3246;
}
.content .col .read-more {
    display: inline-block;
    padding-top: 1rem;
    color: #427dad;
    transition: color 0.3s ease;
}
.col .mega-links li,
.col .mega-links li a {
    padding: 0 1rem;
}
.menu-items li:hover .mega-menu {
    top: 73px;
    opacity: 1;
    visibility: visible;
}
.content .col .read-more:hover {
    color: gray;
}

/* section */
.section {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
