.footer-link:hover::after {
    color: #fff;
    width: 50px;
}

.product-img {
    height: 620px;
    width: 620px;
}
.thumb-area {
    max-height: 620px;
    overflow: auto;
}

@media screen and (max-width: 991px) {
    .product-img {
        height: auto;
        width: auto;
    }
    .thumb-area {
        max-width: 100vw;
        max-height: auto;
        overflow: auto;
    }
}

